.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #ffffffcc;
  backdrop-filter: blur(10px);
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.logo-section {
  display: flex;
  align-items: center;
}

.animated-logo {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: linear-gradient(45deg, #ff6ec4, #7873f5, #007aff, #00c6ff);
  background-size: 300% 300%;
  animation: gradientShift 6s ease infinite, rotate 4s linear infinite, pulse 1.8s ease-in-out infinite;
  margin-right: 10px;
}

.site-name {
  font-weight: 700;
  font-size: 1.3rem;
  letter-spacing: 0.5px;
  background: linear-gradient(45deg, #000000, #333333, #007aff);
  background-size: 200% auto;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textShift 5s ease-in-out infinite;
}

.nav-menu a {
  margin-left: 1rem;
  color: #1d1d1f;
  font-weight: 500;
  transition: color 0.3s;
  text-decoration: none;
}

.nav-menu a:hover,
.nav-menu .active {
  color: #007aff;
}

@keyframes rotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes pulse {
  0%, 100% { box-shadow: 0 0 0 0 rgba(0, 122, 255, 0.6); }
  50% { box-shadow: 0 0 0 10px rgba(0, 122, 255, 0); }
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes textShift {
  0% { background-position: 0% center; }
  50% { background-position: 100% center; }
  100% { background-position: 0% center; }
}
